import React from 'react';
import classNames from 'clsx';
import { ItemContentData, MediaTypes, BaseMetaDataType } from '../../lib';
import {
  MediaRendererProps,
  VideoRendererProps,
  ImageRendererProps,
} from '../types';
import VideoRenderer from './VideoRenderer';
import { getLinkParams, renderCustomOrDefaultComp } from '../utils';
import { DefaultImageRenderer } from './DefaultImageRenderer';
import s from './MediaRenderer.module.scss';

const MediaRenderer: React.FC<MediaRendererProps> = ({
  data,
  selectors,
  customRenderers,
  options,
}) => {
  const { metaData } = data;

  const getInnerMediaItem = (data: ItemContentData<BaseMetaDataType>) => {
    const props = { data, selectors, customRenderers };
    switch (data.type) {
      case MediaTypes.VIDEO:
        return renderCustomOrDefaultComp<VideoRendererProps>(
          customRenderers?.videoRenderer,
          VideoRenderer,
          props as VideoRendererProps,
        );
      case MediaTypes.IMAGE:
      default:
        return renderCustomOrDefaultComp<ImageRendererProps>(
          customRenderers?.imageRenderer,
          DefaultImageRenderer,
          props as ImageRendererProps,
        );
    }
  };
  const innerComp = (
    <>
      {getInnerMediaItem(data)}
      <div className={classNames(s.overlay, selectors?.overlay)}></div>
    </>
  );
  const linkParams = getLinkParams({
    link: metaData.link,
    itemClickAction: options?.itemClickAction,
  });

  return (
    <>
      {linkParams?.href ? (
        <a
          href={linkParams.href}
          target={linkParams.target}
          rel="playground noreferrer"
          tabIndex={-1}
        >
          {innerComp}
        </a>
      ) : (
        <span
          aria-hidden={true} // hide it from assistive technologies for SEO
          rel="playground noreferrer"
          tabIndex={-1}
        >
          {innerComp}
        </span>
      )}
    </>
  );
};

export default MediaRenderer;
